body {
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  background: #fff;
  border-radius: 8px;
  max-width: 960px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

h1, h2 {
  text-align: center;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: 20px 0;
}

th, td {
  text-align: center;
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f4f4f4;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
}

button:hover {
  background-color: #0056b3;
}

form {
  margin-top: 20px;
}

form label {
  margin-bottom: 8px;
  display: block;
}

form input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 10px;
  padding: 6px;
}

.modal-backdrop {
  backdrop-filter: blur(3px);
  z-index: 3;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  z-index: 1000;
  background: #fff;
  border-radius: 10px;
  width: 500px;
  padding: 20px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 5px 15px #0000004d;
}

.modal-info input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  display: block;
}

.modal-button {
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}
